import React from 'react'
import LangageInfo from './LangageInfo'
import Languages from './Languages';

class LanguageListe extends React.Component {
  state = {
    programmes: []
  }

  componentDidMount() {
    this.setState({
      programmes: LangageInfo
    });
  }

  voteHandler = languageId => {
    const voteLanguage = this.state.programmes.map(language => {
      if (languageId === language.id) {
        return Object.assign({}, language, {
          votes: language.votes + 1
        });
      } else {
        return language;
      }
    });

    this.setState({
      programmes: voteLanguage
    });
  };
  render() {
    //sort programmes with highest votes

    const sortprogrammes = this.state.programmes.sort((a, b) => {
      return b.votes - a.votes;
    });
    const programmes = sortprogrammes.map(language => (
        <Languages 
        key = {language.id}
        id = {language.id}
        name = {language.name}
        votes = {language.votes}
        imageUrl = {language.imageUrl}
        logoUrl = {language.logoUrl}
        voteLanguage = {this.voteHandler}
        />
    ));
    return <div className="ui divided unstackable items main">{programmes}</div>;
  }
}

export default LanguageListe;




